<template>
  <div class="container">
    <div class="hourly-card">
      <h4 class="title">{{ getHourlyData.weekday }}</h4>
      <h5>{{ getHourlyData.hourTime }}</h5>
      <h5>{{ getHourlyData.summary }}</h5>
      <p class="text-center">
        <img src="../assets/sunAndClouds.png" />
        <!-- this is working -->
        <img :src="getImage" />
        <!-- this is not working -->
      </p>

      <div class="stats">
        <span>{{ getHourlyData.temperature }} °C</span>
        <span>Feels Like: {{ getHourlyData.feelsLike }} °C</span>
        <span
          >Rain: {{ getHourlyData.rainProb }}% of
          {{ getHourlyData.rainMM }}mm</span
        >
        <span
          >Wind: {{ getHourlyData.windSpeed }}km/h
          {{ getHourlyData.windDirection() }}</span
        >
        <span>UV: {{ getHourlyData.uvIndex }}</span>
      </div>
      <hr />
    </div>
  </div>
</template>

<script>
import sourceData from "@/data.json";

export default {
  data() {
    return {
      hourlyDataList: sourceData.hourly.data,
    };
  },
  computed: {
    // getDate() {
    //   return this.hourlyTimeList.forEach((t) => {
    //     console.log(new Date(t.time * 1000));
    //   });
    // },
    getHourlyData() {
      const event = new Date(this.hourlyDataList[0].time * 1000);
      const weekday = event.toLocaleDateString([], { weekday: "short" });
      const hourTime = event.toLocaleTimeString([], {
        hour12: true,
        hour: "numeric",
      });
      const summary = this.hourlyDataList[0].summary;
      const temperature = Math.round(this.hourlyDataList[0].temperature);
      const feelsLike = Math.round(this.hourlyDataList[0].apparentTemperature);
      const rainProb = this.hourlyDataList[0].precipProbability;
      const rainMM = this.hourlyDataList[0].precipIntensity;
      const windSpeed = Math.round(this.hourlyDataList[0].windSpeed);
      const windDirection = () => {
        const heading = ["N", "NE", "E", "SE", "S", "SW", "W", "NW"];
        let degree = Math.round(this.hourlyDataList[0].windBearing);
        const index =
          Math.round(((degree %= 360) < 0 ? degree + 360 : degree) / 45) % 8;
        return heading[index];
      };
      const uvIndex = Math.round(this.hourlyDataList[0].uvIndex);
      const getImage = this.hourlyDataList[0].icon;
      return {
        weekday,
        hourTime,
        summary,
        temperature,
        feelsLike,
        rainProb,
        rainMM,
        windSpeed,
        windDirection,
        uvIndex,
        getImage,
      };
    },
    getImage() {
      const iconNames = [
        "cloudy-rainy-day",
        "cloudy-day",
        "cloudy-night",
        "foggy-day",
        "rainy-day",
        "sunny-day",
        "partly-cloudy-day",
      ];
      const images = [
        "../assets/cloudAndRain.png",
        "../assets/cloudy.png",
        "../assets/cloudyEvening.png",
        "../assets/fog.png",
        "../assets/rain.png",
        "../assets/sun.png",
        "../assets/sunAndClouds.png",
      ];
      const iconString = this.hourlyDataList[0].icon.toString();
      const index = iconNames.findIndex((i) => i === iconString);
      console.log(images[index]);
      return images[index];
    },
  },
};
</script>

